	module.exports = {"data":{"site":{"siteMetadata":{"title":"My personal site"}},"markdownRemark":{"id":"/home/andrey/development/mysite/src/blog/language-diversity/index.md absPath of file >>> MarkdownRemark","html":"<h2>Language Diversity</h2>\n<p>The fewer languages there are, the more knowledge is accumulated in the remaining languages, and the fewer barries\nthere are for accessing this knowledge. For example, currently the English language enjoys the situation when a huge\n(perhaps the largest) number of book titles, or journals, or films are released in this language. Consequently,\nanyone who understands English has access to all this wealth of knowledge and culture. If (a hypothetical scenario)\na language disappeared, and people who might have spoken it instead joined the pool of English speakers,\nit would have more (in absolute terms) people who could generate knowledge and share it in this language.</p>\n<p>(Compare whatever is currently published in Russian with whatever is published in English — the latter is much\nmore abundant and important.)</p>","frontmatter":{"title":"On linguistic diversity","date":"01 July, 2018"}}},"pathContext":{"slug":"/blog/language-diversity/"}}

